import styled from "styled-components";
import { rem } from "polished";

const toogleActive =
  "data:image/svg+xml;base64,";
const toggle =
  "data:image/svg+xml;base64,";

export const LinkFilter = styled.div`
  display: flex;
  align-items: center;
  color: var(--color-pink-strong);

  & svg {
    margin-right: ${rem("10px")};
    width: ${rem("25px")};
    height: ${rem("36px")};
  }

  @media screen and (max-width: 520px) {
    margin-bottom: ${rem("40px")};
  }
`;

export const ToggleClick = styled.button`
  background: ${(props) =>
    props.active === "all"
      ? `url("${toggle}") no-repeat center / 60px`
      : `url("${toogleActive}") no-repeat center / 60px`};

  width: ${rem("60ppx")};
  height: ${rem("40px")};
  margin: 0px ${rem("25px")};
  cursor: pointer;

  &:focus,
  &:active {
    border: 0;
    box-shadow: none;
    outline: none;
  }

  transition: all 0.2s ease-in-out;
`;
